@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hind+Vadodara&display=swap);
*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header{
  background-image: url(https://images.unsplash.com/photo-1536184071535-78906f7172c2?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60);
  background-repeat: no-repeat;
  object-fit: cover;
  width: 100%;
  height: 80vh;
  background-size: cover;
}
.menu-link{
  display: flex;
  padding: 1.2em;
  justify-content: flex-end;
}
.nav-link{
  list-style: none;
  margin: 0 2em;
  color: whitesmoke;
  font-size: 20px;
}
.zomato-title{
  margin-top: 0.5em;
  font-style: italic;
  color: whitesmoke;
  font-size: 5em;
}
.subheader{
  color: whitesmoke;
  font-weight: 400;
  font-size: 2em;
}
.search,.submit-search,.currectCity{
  margin: 2em 0.3em;
  padding: 0.8em 1.3em;
  width: 30em;
  font-size: 18px;
  border-radius: 15px;
  border: none;
}
.currectCity{
  background-color: white;
  font-size: 1.3em;
  width: 3em;
  height: 1.2em;
}
.submit-search{
  width: 6em;
}
.collections-wrapper{
  margin: 5em 10em;
}
.wrapper{
  margin: 1.5em 0;
}
.collection-title{
  font-size: 3em;
  display: flex;
  justify-content: flex-start;
}
.collection-subtitle{
  display: flex;
  justify-content: flex-start;
}
.card-wrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.card{
  display: flex;
  flex-direction: column;
  width: 16em;
  margin: 20px 10px ;
}
.collection-image{
  width:  100%;
  height: 15em;
  border-radius: 2%;
}
.collection-heading{
  font-size: 1.3em;
  color: black;
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 1.2em;
}
.collectionTemplate{
  width: 80vw;
  margin: 3em auto;
}
.TemplateImage{
  width: 50vw;
  height: 60vh;
}
.TemplateTitle,.TemplateSubtitle,.resto_count{
  display: flex;
  justify-content: flex-start;
}
.TemplateTitle{
  font-size: 2.2em;
}
.TemplateSubtitle{
  font-size: 1.1em;
}
.resto_count{
  margin-top: 1.2em;
  font-size: 1.2em;
}
.resto-card-wrapper{
  display: flex;
  justify-content: space-evenly;
  height: 400px;
  margin:0 5em;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.resto-card{
  width: 300px;
  height: 400px;
  margin: 1em;
  padding: 1em;
  text-align: start;
  background: rgba(0,0,0,0.1);
  font-family: 'Nunito', sans-serif;
}

.resto-image{
  width: 100%;
  height: 200px;
  border-radius: 5%;
}
.resto-title{
  text-align: start;
  font-size: 1.3em;
  font-family: 'Poppins', sans-serif;
}
.zomato-logo{
  margin: 0.5em 5em;
  text-align: start;
  font-size: 2em;
  font-weight: 800;  
  font-style: italic;
}

.resto-card:hover{
  background: rgba(0,0,0,0.3);
  color:black;
}
.restoDetails{
  margin: 1em 10em;
}
hr.style-one {
  border: 0;
  width: 100%;
  height: 1px;
  background: #333;
  background-image: linear-gradient(to right, #ccc, #333, #ccc);
}
.restoFeaturedImage{
  height: 70vh;
  min-width: 60%;
}

.titleFlex{
  display: flex;
  margin: 0.8em 0;
  justify-content: space-between;
}
.restoTitle{
  font-size: 2.5em;
  font-weight: 600;
  display: flex;
  justify-content: flex-start;
}
.restoRating{
  font-size: 1.5em;
}
.restoContentDetails{
  display: flex;
  justify-content: space-between;
  margin: 2em 0;
}
.restoEstablishment{
  font-size: 1.5em;
  text-align: start;
  font-weight: 400;
}
.restoLocation,.restoTimings{
  font-size: 1.1em;
  text-align: start;
  font-weight: 400;  
}
.restoContact{
  color: crimson;
  font-size: 1.2em;
  font-weight: 600;
}
.restoHeader{
  margin-top: 2px;
  font-size: 1.4em;
  font-weight: 400;
}
.restoAverageCost{
  font-size: 1.1em;
  font-weight: 600;
}
.restoCuisines{
  text-align: start;
  margin: 2em 0;
}
.rating-section{
  width: 80vw;
  margin: 2em auto;
}
.headerprofile-flex{
  display: flex;
  justify-content: space-between;
}
.review_profile-flex{
  display: flex;
}
.review_profile{
  width: 5em;
  border-radius: 50%;
}
.review_username,.review_rating{
  font-size: 1.2em;
  text-align: start;
  margin-left: 1em;
  margin-top: 0.5em;
}
.review_response{
  display: flex;
  margin: 2em 0;
  font-weight: 300;
}

.review_time_friendly{
  font-weight: 300;
  font-size: 0.8em;
}
.rating_text{
  margin:2em 0 ;
  text-align: justify;
}

.review_section_footer span{
 margin-right: 5em;
}
.review_section_footer{
  margin: 0 ;
  display: flex;
  justify-content: space-between;
}
.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  margin:30vh auto; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

